$sidebar-width: 450px;
$sidebar-top: 90px;
$header-height: 50px;



.header {
    background-color: RGBA(71, 187, 179, 1.0);
    height: $header-height;
    top: 0;
    left: 0;
    .Title {
        position: relative;
        text-align: center;
        vertical-align: middle;
        line-height: $header-height;
        font-family: "Abril Fatface", "Times New Roman" , serif;
        font-size: 40px;
        letter-spacing: .07em;
        color: RGBA(242, 239, 234, 1.00); } }

.container {
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 20px;
    line-height: $header-height;
    background-color: RGBA(255, 232, 209, 1.00); }

a {
    color: RGBA(242, 239, 234, 1.00);
    text-decoration: inherit;
    font-weight: inherit; }

.articleWrapperDiv {
    padding-left: 10px;
    margin-top: 10px;
    background-color: RGBA(71, 187, 179, 1.00);
